@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code&display=swap);
*,::after,::before{box-sizing:border-box}html,body,#root{height:100%;width:100%}body{line-height:1.5;font-family:"Poppins",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1rem;font-weight:400;color:white;margin:0;background-image:url(/static/media/bg.daf4188c.jpg);background-repeat:no-repeat;background-size:cover;background-position:top center}body::before{content:'';position:absolute;display:block;height:100%;width:100%;background:rgba(0,0,0,0.7);z-index:-1}code,.code{line-height:1.5;font-size:1rem;font-family:"Fira Code",source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace;font-weight:400;font-feature-settings:'calt' 1;text-rendering:optimizeLegibility}#root{display:flex;flex-direction:column}#root main{flex-shrink:0}#root footer{margin-top:auto}header{padding:1rem 2rem;display:grid;grid-gap:1rem;gap:1rem;grid-template-columns:10rem auto;grid-template-rows:3.75rem;z-index:1}header .brand{width:10rem}header .navbar{text-align:right}header .navbar ul{padding:0;list-style:none;margin:0}header .navbar ul>*{display:inline-block;position:relative}header .navbar .nav-item a,header .navbar .nav-item button{-webkit-user-select:none;-ms-user-select:none;user-select:none;color:white;text-decoration:none;border:none;background:transparent;padding:1rem 1.5rem;font-size:1rem;font-family:"Poppins",sans-serif;display:block;cursor:pointer;transition:0.25s;position:relative;border-radius:6px}header .navbar .nav-item a:hover,header .navbar .nav-item button:hover{background:rgba(255,255,255,0.1)}header .navbar .nav-item a.active::before{content:'';position:absolute;background-color:white;width:50%;height:2px;bottom:0.5rem;left:50%;transform:translateX(-50%)}header .navbar .nav-item .lang-button:hover{background-color:transparent}header .navbar .nav-item .lang-button::before{content:'';border:solid white;border-width:0 2px 2px 0;display:inline-block;padding:3px;transform:rotate(45deg) translateY(-50%);margin-right:0.5rem;margin-left:-0.5rem}header .navbar .lang-selector{position:absolute;right:0;background-color:rgba(255,255,255,0.1);box-shadow:0px 5px 20px -10px #000;border-radius:6px 0 6px 6px}header .navbar .lang-selector::before{content:'';width:0;height:0;border-left:10px solid transparent;border-bottom:10px solid rgba(255,255,255,0.1);position:absolute;right:0;top:-10px}header .navbar .lang-selector .nav-item{width:100%}header .navbar .lang-selector .nav-item>*{width:100%;padding-right:3rem}header .navbar .lang-selector .nav-item>* svg{right:1.3rem}header .navbar svg{max-width:1.5rem;display:block;position:absolute;right:0;top:50%;transform:translateY(-50%)}main .container{padding:1rem 2rem;max-width:1400px;margin:0 auto}footer{padding:1rem 2rem}

